@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900);
* {
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

body {
    color: #332E2A;
    line-height: 1.75em;
}

h1 {
    font-size: 60px;
    font-weight: 700;
}

h2 {
    font-size: xx-large;
    font-weight: 700;
}

h3 {
    font-size: large;
    font-weight: 600;
    line-height: 1.5em;

    margin-bottom: 25px;
}

h4 {
    font-size: large;
    font-weight: 500;
}

h5 {
    font-size: medium;
    font-weight: 600;
    line-height: 2em;
}

h6 {
    font-size: large;
    font-weight: 300;
    line-height: 2em;
}

p {
    font-size: medium;
    font-weight: 300;
}

a {
    text-decoration: none;
}

a:link {
    color: #332E2A;
}

a:visited {
    color: #332E2A;
}

a:hover {
    text-decoration: none;
    color: #B03E30;
}

a:active {
    color: #332E2A;
}

.spacer {
    flex: 1 1;
}

.hero {
    padding: 12% 10% 4% 10%;
}

.triangle {
    width: 100%;
    padding: 0;
    margin: 0;
}

.grey-background {
    background-color: #F8F8F8;
}

@media only screen and (max-width: 768px) {
    h1 {
        font-size: 50px;
    }

    .hero {
        padding: 20% 10% 8% 10%;
    }
}
.header {
    width: 100%;
    padding: 3% 4%;
}

.header__logo {
    width: 250px;
}

.header__hamburger {
    display: none;
}

.header__hamburger-menu {
    width: 60%;
    height: 100%;

    margin-left: 40%;
    padding: 10%;

    outline: none;
    overflow: scroll;
    background-color: white;
}

.header__menu {
    display: inline-flex;
}

.header__menu-item {
    text-decoration: none;
    margin-top: 1.5%;
    margin-left: 10%;
}

.header__menu-item:link {
    color: #332E2A;
}

.header__menu-item:visited {
    color: #332E2A;
}

.header__menu-item:hover {
    text-decoration: none;
    color: #B03E30;
}

.header__menu-item:active {
    color: #332E2A;
}


@media only screen and (max-width: 768px) {
    .header {
        padding: 4% 5%;
    }

    .header__logo {
        width: 220px;
    }

    .header__hamburger {
        display: inline-flex;
    }

    .header__menu {
        display: none;
    }
}

@media only screen and (max-width: 576px) {
    .header {
        padding: 4% 2%;
    }
}

.home__hero {
    padding: 29% 8% 2% 8%;
}

.home__subtitle {
    color: #B03E30;
    font-size: 42px;
    margin-top: 12px;
}

.home__section-header {
    font-size: 42px;
    margin: 0% 2%;
}

.home__card {
    text-align: center;
    padding: 3%;
    margin: 3%;

    background-color: white;
    box-shadow: rgba(51, 46, 42, 0.25) 0px 3px 10px;
}

.home__about {
    padding: 12% 10% 5% 10%;
}

.home__about-icon {
    height: 85px;
    margin: 25px 20px 30px 20px;
}

.home__about-number {
    background: url(/static/media/number01.3f9f0cae.svg);
    background-size: 40%;
    background-position-x: 8%;
    background-position-y: 20%;
    background-repeat: no-repeat;
    background-color: #F8F8F8;
}

.home__about-border {
    background: url(/static/media/border01.b56d5397.svg);
    background-size: 50%;
    background-position-x: 98%;
    background-position-y: 100%;
    background-repeat: no-repeat;
}

.home__event {
    padding: 15% 7% 8% 0%;
}

.home__event-card {
    padding: 12%;
    margin: 0% 0% 10% 3%;
}

.home__event-before {
    display: none;
}

.home__event-after {
    padding-left: 5%;
}

.home__event-number {
    background: url(/static/media/number02.8eb7dd0f.svg);
    background-size: 52%;
    background-position-x: 78%;
    background-position-y: 30%;
    background-repeat: no-repeat;
}

.home__event-border {
    background: url(/static/media/border02.616ebce8.svg);
    background-size: 10%;
    background-position-y: 100%;
    background-repeat: no-repeat;
}

.home__member {
    padding: 12% 10% 10% 10%;
}

.home__member-card {
    cursor: pointer;
    padding: 0;
}

.home__member-image {
    width: 100%;
    margin-bottom: 20px;
}

.home__member-modal {
    text-align: center;
    justify-items: baseline;
    background-color: white;
    outline: none;
    overflow: scroll;
    overflow-x: hidden;

    width: 60%;

    padding: 3%;
    margin-top: 8%;
}

.home__member-modal-image {
    width: 100%;
}

.home__member-number {
    background: url(/static/media/number03.fbfc3eb0.svg);
    background-size: 48%;
    background-position-x: 8%;
    background-position-y: 4%;
    background-repeat: no-repeat;
    background-color: #F8F8F8;
}

.home__member-border {
    background: url(/static/media/border03.a24f4a9d.svg);
    background-size: 50%;
    background-position-x: 98%;
    background-position-y: 100%;
    background-repeat: no-repeat;
}


@media only screen and (max-width: 1200px) {
    .home__about {
        padding: 12% 2% 5% 2%;
    }

    .home__member {
        padding: 12% 5% 10% 5%;
    }

    .home__member-modal {
        width: 65%;
        height: 62%;
    }
}

@media only screen and (max-width: 992px) {
    .home__hero {
        padding: 29% 10% 8% 10%;
    }

    .home__subtitle {
        font-size: 32px;
    }

    .home__card {
        padding: 6%;
        margin: 4%;
    }

    .home__about {
        padding: 12% 20% 8% 20%;
    }

    .home__about-number {
        background-size: 50%;
        background-position-x: 15%;
        background-position-y: 6%;
    }

    .home__about-border {
        background-size: 90%;
        background-position-x: 180%;
        background-position-y: 98%;

        padding: 6%;
    }

    .home__event {
        padding: 15% 0% 8% 0%;
    }

    .home__event-card {
        padding: 15%;
        margin: 8% 0% 10% 1%;
    }

    .home__event-before {
        display: inline;
    }

    .home__event-after {
        display: none;
    }

    .home__event-number {
        background-size: 60%;
        background-position-y: 12%;
    }

    .home__member {
        padding: 12% 2% 10% 2%;
    }

    .home__member-card {
        padding: 0;
        margin: 3%;
    }

    .home__member-modal {
        width: 70%;
    }

    .home__member-number {
        background-size: 60%;
        background-position-y: 2%;
    }
}

@media only screen and (max-width: 768px) {
    .home__event-card {
        padding: 15%;
        margin: 8% 0% 15% 0%;
    }

    .home__event-number {
        background-size: 60%;
        background-position-y: 12%;
    }

    .home__member {
        padding: 20% 8% 10% 8%;
    }

    .home__member-card {
        margin: 3%;
    }

    .home__member-modal {
        width: 55%;
        height: 85%;
        margin-top: 6%;
    }

    .home__member-modal-image {
        margin-bottom: 10%;
    }
}

@media only screen and (max-width: 576px) {
    .home__hero {
        padding: 29% 10% 12% 10%;
    }

    .home__card {
        padding: 6%;
        margin: 5%;
    }

    .home__about {
        padding: 25% 5% 20% 5%;
    }

    .home__about-number {
        background-size: 70%;
        background-position-x: 12%;
        background-position-y: 6%;
    }

    .home__about-border {
        background-size: 90%;
        background-position-x: 180%;
        background-position-y: 99%;

        padding: 5%;
    }

    .home__event {
        padding: 30% 0% 8% 0%;
    }

    .home__event-card {
        padding: 12%;
        margin: 10% 0% 10% 0%;
    }

    .home__event-number {
        background-size: 78%;
        background-position-y: 10%;
    }

    .home__member {
        padding: 30% 12% 10% 12%;
    }

    .home__member-card {
        padding: 0;
        margin: 8%;
    }

    .home__member-modal {
        width: 100%;
        height: 100%;
        margin-top: 0%;
    }

    .home__member-modal-image {
        margin-bottom: 10%;
    }

    .home__member-number {
        background-size: 78%;
        background-position-x: 12%;
        background-position-y: 1.25%;
    }
}
.post__subheader {
    padding-top: 2%;
}

.post__body {
    padding: 5%;
    padding-bottom: 8%;
}

.post__card {
    text-align: center;

    margin: 3%;
    padding: 4% 3%;

    background-color: white;
    box-shadow: rgba(51, 46, 42, 0.25) 0px 3px 10px;
}

.post__modal {
    text-align: center;
    background-color: white;
    outline: none;
    overflow: scroll;

    height: 100%;
    width: 75%;
    
    padding: 2%;
}

.post__modal-line {
    margin-top: 3%;
}

.post__modal-content {
    margin: 0% 8%;
}

.post__button {
    color: white;
}

.post__button:link {
    color: white;
}

.post__button:visited {
    color: white;
}

.post__button:hover {
    color: white;
}

.post__button:active {
    color: white;
}

.mode-button {
    cursor: pointer;
    text-align: right;
    color: #B03E30;
}

.mode-button:hover {
    color: #B03E30;
}

.mode-button-off {
    color: #332E2A;
}


@media only screen and (max-width: 1200px) {
    .post__subheader {
        padding-top: 5%;
    }

    .post__body {
        padding: 5% 0%;
        padding-bottom: 8%;
    }
    
    .post__card {    
        margin: 4%;
        padding: 5% 4%;
    }
}

@media only screen and (max-width: 768px) {
    .post__subheader {
        padding-top: 8%;
        padding-bottom: 0%;
    }

    .post__body {
        padding: 6%;
        padding-bottom: 15%;
    }
    
    .post__card {    
        padding: 8% 6%;
    }
}

@media only screen and (max-width: 576px) {
    .post__subheader {
        padding-top: 12%;
        padding-bottom: 5%;
    }

    .post__body {
        padding-bottom: 18%;
    }
    
    .post__card {   
        margin: 6%; 
        padding: 10% 8%;
    }

    .post__modal {
        text-align: left;
        width: 100%;
        padding: 5%;
    }

    .post__modal-content {
        margin: 0% 5%;
    }
}
.contact {
    width: 100%;
}

.contact__subheader {
    text-align: right;
    padding: 3% 8%;
}

.contact__body {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 5%;
    margin-bottom: 8%;
    padding: 3% 5%;

    background-color: white;
    box-shadow: rgba(51, 46, 42, 0.25) 0px 3px 10px;
}


@media only screen and (max-width: 768px) {
    .contact__subheader {
        text-align: right;
        padding: 10% 8%;
        background-color: #F8F8F8;
    }

    .contact__body {
        margin-top: 0%;
        margin-bottom: 18%;
    }
}
.faq {
    width: 100%;
}

.faq__subheader {
    text-align: right;
    padding: 3% 8%;
}

.faq__body {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 8% 17%;
}

.faq__topic {
    width: 100%;
    padding: 6% 8% 4% 8%;
    margin-bottom: 5%;

    background-color: white;
    box-shadow: rgba(51, 46, 42, 0.25) 0px 3px 10px;
}


@media only screen and (max-width: 768px) {
    .faq__subheader {
        text-align: right;
        padding: 10% 8%;
        background-color: #F8F8F8;
    }

    .faq__body {
        padding: 0% 10%;
    }

    .faq__topic {
        padding: 10% 12%;
        margin-bottom: 10%;
    }
}
.footer {
    width: 100%;
    padding: 5% 12%;
}

.footer__section {
    margin: 4% 0%;
}

.footer__line {
    content: '';
    border-bottom: 1px solid #332E2A;
    margin-top: 20px;
}

.footer__social {
    display: inline-flex;
    margin-top: 25px;
}

.footer__icon {
    margin: 4px 6px;
}

.footer__copyright {
    display: inline-flex;
    margin-top: 12px;
}


@media only screen and (max-width: 576px) {
    .footer {
        padding: 10% 15%;
    }
}
